<template>
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 bg-gradient-success">
        <div id="searchDiv">
            <h1 style="color:white;">
                실거래가 상세정보
            </h1>
            <br>
                <b-button variant="primary">아파트별 검색</b-button>
                <b-button variant="primary">동별 검색</b-button>

            <br><br><br>
                <div>
                    <b-dropdown
                        split="split"
                        split-variant="outline-primary"
                        variant="primary"
                        text="시도"
                        class="m-2">
                        <b-dropdown-item href="#">Action</b-dropdown-item>
                        <b-dropdown-item href="#">Another action</b-dropdown-item>
                        <b-dropdown-item href="#">Something else here...</b-dropdown-item>
                    </b-dropdown>
                    <b-dropdown
                        split="split"
                        split-variant="outline-primary"
                        variant="primary"
                        text="구군"
                        class="m-2">
                        <b-dropdown-item href="#">Action</b-dropdown-item>
                        <b-dropdown-item href="#">Another action</b-dropdown-item>
                        <b-dropdown-item href="#">Something else here...</b-dropdown-item>
                    </b-dropdown>
                    <b-dropdown
                        split="split"
                        split-variant="outline-primary"
                        variant="primary"
                        text="읍면동"
                        class="m-2">
                        <b-dropdown-item href="#">Action</b-dropdown-item>
                        <b-dropdown-item href="#">Another action</b-dropdown-item>
                        <b-dropdown-item href="#">Something else here...</b-dropdown-item>
                    </b-dropdown>
                    <b-dropdown text="Block Level Dropdown" block variant="primary" class="m-2">
                    <b-dropdown-item href="#">Action</b-dropdown-item>
                    <b-dropdown-item href="#">Another action</b-dropdown-item>
                    <b-dropdown-item href="#">Something else here</b-dropdown-item>
                    </b-dropdown>
                </div>

            </div>
            <div id="app">
                <div style="height:10px;"></div>
                <div id="map"></div>
            </div>
        </base-header>
    </template>

    <script>
        export default {
            mounted() {
                if (window.kakao && window.kakao.maps) {
                    this.initMap();
                } else {
                    const script = document.createElement('script');
                    /* global kakao */
                    script.onload = () => kakao
                        .maps
                        .load(this.initMap);
                    script.src = 'http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=195435a07468b8970e4' +
                            '24ff1e357290b';
                    document
                        .head
                        .appendChild(script);
                }
            },
            methods: {
                initMap() {
                    var container = document.getElementById('map');
                    var options = {
                        center: new kakao
                            .maps
                            .LatLng(33.450701, 126.570667),
                        level: 3
                    };

                    var map = new kakao
                        .maps
                        .Map(container, options);
                    map.setMapTypeId(kakao.maps.MapTypeId.HYBRID);
                }
            }
        }
    </script>

    <style>
        #searchDiv {
            text-align: center;
            margin: 0 auto;
        }
        #map {
            width: 100%;
            height: 500px;
        }
    </style>